import React, { useState } from "react";
import MealItem from "./MealItem";
import "./meal.css";
import Random from "./Random";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { faShuffle } from "@fortawesome/free-solid-svg-icons";

const Meal = () => {
  const [search, setSearch] = useState("");
  const [meal, setMeal] = useState();
  const [mealvisible, setMealvisible] = useState([]);

  function mealData() {
    fetch(`https://www.themealdb.com/api/json/v1/1/search.php?s=${search}`)
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        setMeal(data.meals);
      });
  }

  function randomDish() {
    fetch(`https://www.themealdb.com/api/json/v1/1/random.php`)
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        setMealvisible(data.meals);
      });
  }
  return (
    <div className="mealFinder">
      <h1 className="heading">Meal Finder </h1>
      <input
        type="text"
        className="search"
        placeholder="search for the meal"
        onChange={(e) => setSearch(e.target.value)}
        value={search}
      />
      <button type="search" onClick={() => mealData()}>
        <FontAwesomeIcon icon={faSearch} />
      </button>

      <button type="search" onClick={randomDish}>
        <FontAwesomeIcon icon={faShuffle} />
      </button>
      <div className="container">
        {meal
          ? meal.map((response) => {
              return <MealItem data={response} />;
            })
          : ""}
      </div>

      {mealvisible.map((list) => {
        return <Random data={list} />;
      })}
    </div>
  );
};

export default Meal;
